import React, { useState } from 'react';
import Name from './NameAnimation'

import github from './Icons/icons8-github-100.png'
import Typist from 'react-typist';
import resume from './Jalomo_Resume.pdf'
import './type.scss'
import {useSpring, animated} from 'react-spring'

// Styled-components lets you write actual CSS in your JavaScript
import styled, { ThemeProvider } from 'styled-components'

import './App.css'; //background-color: #000015;


const themes = ["palevioletred", "mediumseagreen", "violet", "slateblue", "deepskyblue", "paleturquoise", "silver", "navajowhite"]

const Icon = styled.div`
  width: 9ch;
  height: 9ch;
  background: transparent;
  border-radius: ${props => props.radius};
  background-size: cover;
  background-position: center center;
  background-image: url(${props => props.icon});
  transition: box-shadow 0.5s;
  will-change: transform;
`



const Button = styled.button`
  background: transparent;
  border-radius: 3px;
  margin: 0 1em;
  font-size: 18px;
  padding: 0.25em 1em;
  cursor: pointer;

  /* Color the border and text with theme.main */
  color: ${props => props.theme.main};
  border: 2px solid ${props => props.theme.main};

  transition: 0.5s all ease-out;

  &:hover {
    background: solid;
    background-color: ${props => props.theme.main};
    border: 3px solid ${props => props.theme.main};
    color: white;
    box-shadow: 0 12px 16px 0 rgba(192,192,192,0.3), 0 17px 50px 0 rgba(192,192,192,0.1);
  }
`


// We are passing a default theme for Buttons that arent wrapped in the ThemeProvider
Button.defaultProps = {
  theme: {
    main: "palevioletred"
  }
}


const Container = styled.div`
  text-align: right;
  text-center: center;
  padding:30px; 
  background: linear-gradient(to bottom, rgb(11, 11, 41), rgba(11, 11, 41,.0));
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1030;
`


function getRandomInt(max) {
  return Math.floor(Math.random() * Math.floor(max));
}

const calc = (x, y) => [-(y - window.innerHeight / 2) / 20, (x - window.innerWidth / 2) / 20, 1.1]
const trans = (x, y, s) => `perspective(600px) rotateX(${x}deg) rotateY(${y}deg) scale(${s})`


function App() {
  const [icon_Spring, set] = useSpring(() => ({ xys: [0, 0, 1], config: { mass: 5, tension: 350, friction: 40 } }))
  const props = useSpring({ friction: 100, tension: 5, delay: 1800, opacity: 1, from: {opacity: 0}})
  //const props2 = useSpring({ value: 100, from: { value: 0 } })


  // Defining a theme for Styled components, Random every request.
  const theme = {
    main: themes[getRandomInt(themes.length)]
  }

//<img src={ball} alt="ball" height={ballGifSize} width={ballGifSize}/>


  return (
    <div className="App">
      <Name themeColor={theme.main}/>
        <animated.div style={props}>
          <ThemeProvider theme={theme}>
            <Container>
              <a className="nav-item" href="#About">
                <Button> About </Button>
              </a>
              <a className="nav-item" href="#Projects">
                <Button> Projects </Button>
              </a>
              <a href={resume} target='_blank' rel='nofollow noopener noreferrer' >
                <Button> Resume </Button>
              </a>
              <a className="nav-item" href="#Contact">
                <Button> Contact </Button>
              </a>
            </Container>
            <div id="About" className="section" style={{marginTop: 500, color: theme.main}}>
              <h2> About Me </h2>
              <div className="section-sub">
                <p> Im a savage classy boujee ratchet </p>
              </div>
            </div>
            <div id="Projects" className="section" style={{color: theme.main}}>
              <h2> My Projects </h2>
              <div className="section-sub">
                <p> This website LOL </p>
              </div>
            </div>
            <div id="Contact" className="section" style={{color: theme.main}}>
              <h2> Contact </h2>
              <div className="section-sub">
                <p> 8725882999 </p>

                <img src={github}/>

                <animated.div
                  
                  onMouseMove={({ clientX: x, clientY: y }) => set({ xys: calc(x, y) })}
                  onMouseLeave={() => set({ xys: [0, 0, 1] })}
                  style={{ transform: icon_Spring.xys.interpolate(trans),  }}
                >
                <Icon radius='100px' icon={github} />
                </animated.div>
                
              </div>
            </div>
          </ThemeProvider>
        </animated.div>
        
    </div>
  );
}

export default App;
